import React, { Component } from 'react';
import { Col, Form, Input, Select } from 'antd';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);
@Form.create({ name: 'FormInfoAddrBasic' })
class FormInfoTS extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      // labelCol: {
      //   xs: { span: 24 },
      //   sm: { span: 6 },
      // },
      // wrapperCol: {
      //   xs: { span: 24 },
      //   sm: { span: 18 },
      // },
      // colon: false,
    };
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ngoại ngữ">
            {getFieldDecorator('system1', { initialValue: 'Tiếng Anh' })(createSelect('Ngoại ngữ'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Khu vực">
            {getFieldDecorator('system2', { initialValue: 'KV 1' })(createSelect('Khu vực'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Đối tượng dự">
            {getFieldDecorator('system3', { initialValue: '' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Số báo danh">
            {getFieldDecorator('system4', { initialValue: 'BD914' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Điểm tổng cộng">
            {getFieldDecorator('system5', { initialValue: '21' })(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoTS;



