/* eslint-disable */
import React from 'react';
import { Button, Calendar, Tabs, Upload, Icon, Input, Menu, Dropdown } from 'antd';
import Avatar from 'components/CleanComponents/Avatar';
import Chat from 'components/CleanComponents/Chat';
import data from './data.json';
import './style.scss';
import SettingsForm from './SettingsForm';
import FormInfoBasic from './FormInfoBasic';
import FormInfoBasic1 from './FormInfoBasic_1';
import FormInfoAddrBasic from './FormInfoAddrBasic';
import FormInfoTS from './FormInfoTS';
import { getStudentById } from 'services/student';
const { TabPane } = Tabs;
const { TextArea } = Input;

const actions = (
  <Menu>
    <Menu.Item>
      <Icon type="edit" /> Edit Post
    </Menu.Item>
    <Menu.Item>
      <Icon type="delete" /> Delete Post
    </Menu.Item>
    <Menu.Item>
      <Icon type="frown-o" /> Mark as a Spam
    </Menu.Item>
  </Menu>
);

class ProfileApp extends React.Component {
  state = {
    photo: '',
    background: '',
    student: {},
  };

  componentWillMount() {
    this.setState({
      photo: data.photo,
      background: data.background,
    });
  }
  componentDidMount = async () => {
    const { data } = await getStudentById({ id: 43 });
    console.log(data);
    this.setState({ student: data });
  };

  render() {
    const { photo, background, student } = this.state;
    return (
      <div className="profile">
        <div className="row">
          <div className="col-xl-4">
            <div className="card profile__header" style={{ backgroundImage: `url(${background})` }}>
              <div className="profile__header-card">
                <div className="card-body text-center">
                  <Avatar src={photo} size="110" border="true" borderColor="white" />
                  <br />
                  <br />
                  <Button.Group size="default">
                    <Button style={{ width: 150 }}>{student.fullname}</Button>
                    <Button style={{ width: 150 }}>MSSV : {student.code}</Button>
                  </Button.Group>
                  <br />
                </div>
              </div>
            </div>
            <div className="card">
              <div className="card-body">
                <div className="profile__actions">
                  <FormInfoBasic1 student={student} />
                </div>
              </div>
            </div>
            <div className="card">
              <div className="card-body">
                <h5 className="mb-3 text-black">
                  <strong>Lịch</strong>
                </h5>
                <Calendar fullscreen={false} />
              </div>
            </div>
          </div>
          <div className="col-xl-8">
            <div className="card">
              <div className="card-body">
                <Tabs defaultActiveKey="1">
                  <TabPane
                    tab={
                      <span>
                        <i className="icmn-menu" /> Thông tin
                      </span>
                    }
                    key="1"
                  >
                    <div className="">
                      <div className="card">
                        <div className="card-body">
                          <h5 className="mb-3 text-black">
                            <strong>Thông tin lý lịch</strong>
                          </h5>
                          <div className="profile__actions">
                            <FormInfoBasic student={student} />
                          </div>
                        </div>
                      </div>
                      <div className="card">
                        <div className="card-body">
                          <h5 className="mb-3 text-black">
                            <strong>Thông tin nơi chốn</strong>
                          </h5>
                          <div className="profile__actions">
                            <FormInfoAddrBasic />
                          </div>
                        </div>
                      </div>
                      <div className="card">
                        <div className="card-body">
                          <h5 className="mb-3 text-black">
                            <strong>Thông tin tuyển sinh</strong>
                          </h5>
                          <div className="profile__actions">
                            <FormInfoTS />
                          </div>
                        </div>
                      </div>
                    </div>
                    <hr />
                  </TabPane>
                  {/* <TabPane
                    tab={
                      <span>
                        <i className="icmn-bubbles" /> Học tập
                      </span>
                    }
                    key="2"
                  >
                    <Chat />
                  </TabPane>
                  <TabPane
                    tab={
                      <span>
                        <i className="icmn-cog" /> Rèn luyện
                      </span>
                    }
                    key="3"
                  >
                    <SettingsForm />
                  </TabPane>
                  <TabPane
                    tab={
                      <span>
                        <i className="icmn-cog" /> Tài chính
                      </span>
                    }
                    key="4"
                  >
                    <SettingsForm />
                  </TabPane>
                  <TabPane
                    tab={
                      <span>
                        <i className="icmn-cog" /> Tốt nghiệp
                      </span>
                    }
                    key="5"
                  >
                    <SettingsForm />
                  </TabPane> */}
                </Tabs>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ProfileApp;



