import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { SelectGender, InputGroupDate } from 'components/common/index';

@Form.create({ name: 'FormInfoBasic' })
class FormInfoBasic extends Component {
  state = {};

  componentWillReceiveProps = nextProps => {
    if (nextProps.student !== this.props.student) {
      const { setFieldsValue } = this.props.form;
      const { student } = { ...nextProps };
      setFieldsValue(student);
      console.log(student);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {};
    return (
      <Form layout="vertical">
        <Col span={24}>
          <Form.Item {...formItemLayout} label="Ngày sinh">
            {getFieldDecorator('dob', {})(<InputGroupDate style={{ width: '100%' }} />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="Giới tính">
            {getFieldDecorator('gender', {})(<SelectGender />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="ĐT cá nhân">
            {getFieldDecorator('phone', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="Email">
            {getFieldDecorator('email', {})(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoBasic;



