import React, { Component } from 'react';
import { Col, Form, Input, Select } from 'antd';
import { SelectAjax } from '../../../../components/common/index';
import { getListTown } from '../../../../services/town';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);

@Form.create({ name: 'FormInfoAddrBasic' })
class FormInfoAddrBasic extends Component {
  state = {};

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {};
    return (
      <Form layout="vertical">
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quê quán">
            {getFieldDecorator('homeVillage', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quốc tịch">
            {getFieldDecorator('nationId', {})(createSelect('Quốc tịch'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="TP xuất thân">
            {getFieldDecorator('comeFromId', {})(createSelect('Thành phố'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ĐC thường trú">
            {getFieldDecorator('permanentAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Xã/Phường">
            {getFieldDecorator('village', {})(createSelect('Xã'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tỉnh/Tp">
            {getFieldDecorator('townId', {})(<SelectAjax service={getListTown} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quận/Huyện">
            {getFieldDecorator('districtId', {})(createSelect('Huyện'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Địa chỉ báo tin">
            {getFieldDecorator('emergencyAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nơi ở hiện nay">
            {getFieldDecorator('liveAddress', {})(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoAddrBasic;



