import React, { Component } from 'react';
import { Col, Form, Input, DatePicker } from 'antd';
import moment from 'moment';
import { getPolicyObjects } from 'services/policyObject';
import { SelectAjax } from '../../../../components/common/index';
import { getEthnicity } from '../../../../services/ethnicity';
import { getSubsidizeObjects } from '../../../../services/subsidizeObject';
import { getPrioritizeObjects } from '../../../../services/prioritizeObject';

@Form.create({ name: 'FormInfoBasic' })
class FormInfoBasic extends Component {
  state = {};

  componentWillReceiveProps = nextProps => {
    if (nextProps.student !== this.props.student) {
      const { setFieldsValue } = this.props.form;
      const { student } = { ...nextProps };
      student.dateIssue = moment(student.dateIssue);
      student.unionDate = moment(student.unionDate);
      student.politicalDate = moment(student.politicalDate);
      setFieldsValue(student);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = null;
    return (
      <Form layout="vertical">
        <Col span={12}>
          <Form.Item {...formItemLayout} label="CMND">
            {getFieldDecorator('idCard', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nơi cấp">
            {getFieldDecorator('placeIssue', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ngày cấp">
            {getFieldDecorator('dateIssue', {})(<DatePicker style={{ width: '100%' }} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Dân tộc">
            {getFieldDecorator('ethnicityId', {})(<SelectAjax service={getEthnicity} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tôn giáo">
            {getFieldDecorator('religion', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Đoàn">
            {getFieldDecorator('unionDate', {})(<DatePicker style={{ width: '100%' }} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Đảng">
            {getFieldDecorator('politicalDate', {})(<DatePicker style={{ width: '100%' }} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ĐT chính sách">
            {getFieldDecorator('policyObjectId', {})(<SelectAjax service={getPolicyObjects} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ĐT TC">
            {getFieldDecorator('subsidizeObjectId', {})(
              <SelectAjax service={getSubsidizeObjects} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Điện thoại nhà riêng">
            {getFieldDecorator('homePhone', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nhóm đối">
            {getFieldDecorator('prioritizeObjectId', {})(
              <SelectAjax service={getPrioritizeObjects} />
            )}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoBasic;



